import Vue from 'vue';
import store from '../../store';
import config from '../../config';

/**
 * 
 * @param {*} authConfig 
 */
export function has(authConfig) {
  // 
  if (!config.enabledButtonAuth) return true;
  // 
  if (!authConfig || !authConfig.length) return true;
  const authList = (store.getters && store.getters.authList) || [];
  const hasPermission = authConfig.some(role => {
    return authList.indexOf(role) >= 0;
  });
  return hasPermission;
}

// 
Vue.has = has;
Vue.prototype.$has = has;
