/**
 *  
 * vuexauthmenuList
 */
import Vue from 'vue';
import store from '../../store';
import config from '../../config';
/**
 * dom
 * @param {*} el dom
 * @param {*} binding 
 */

function has(el, binding) {
  // 
  if (!config.enabledButtonAuth) return;
  var value = binding.value;
  var menuList = store.getters && store.getters.menuList || [];

  if (value && value instanceof Array && value.length > 0) {
    var permissionrules = value;
    var hasPermission = menuList.some(function (role) {
      return permissionrules.indexOf(role.menuId + '') >= 0; //
    });

    if (!hasPermission && value && value.length) {
      el.parentNode && el.parentNode.removeChild(el);
    }
  }
}

var menu = {
  inserted: has,
  update: has
};

var install = function install(Vue) {
  Vue.directive('menu', menu);
};

Vue.use(install);