import axios from '@/plugins/axios';

export function login(data) {
  return axios(
    {
      url: '/login/toLogin',
      method: 'POST',
      data
    },
    'bonus-be'
  );
}

/**
 * 
 * @param  {Object} params 
 * @param  {String} params.newPass 
 */
export function updatePass(params) {
  return axios(
    {
      url: '/user/updatePass',
      method: 'GET',
      params
    },
    'bonus-be'
  );
}

