import Vue from 'vue';
import store from '../../store/index';

/**
 * 
 * @param {*} key 
 * @param {*} type 
 */
export function dict(...args) {
  if (args.length === 1) {
    // 
    return store.getters['dict/getList'](args[0]);
  } else {
    return store.getters['dict/queryLabel']({
      type: args[0],
      key: args[1]
    });
  }
}

// 
Object.defineProperty(Vue.prototype, '$D', {
  value: dict
});
